.messenger {
    display: flex;
    flex-direction: column;
    padding: 20px; 
    border-radius: 10px; 
    background-color: #ffffff; /* Белый фон для мессенджера */
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.chat-name {
    font-size: 24px; /* Увеличиваем размер шрифта */
    font-weight: bold; /* Делаем текст жирным */
    color: #00796b; /* Цвет текста */
    margin-top: -10px;
    margin-bottom: 15px; /* Отступ снизу */
    text-align: center; /* Центрируем текст */
    padding: 10px; /* Добавляем отступы для улучшения внешнего вида */
    border-bottom: 2px solid #0288d1; /* Добавляем линию снизу */
    background-color: rgba(255, 255, 255, 0.8); /* Полупрозрачный фон для контраста */
    border-radius: 5px; /* Скругляем углы */
}

.user-creation {
    margin-bottom: 15px; 
    display: flex;
    gap: 10px; 
    flex-wrap: wrap; 
    /*align-items: center;  Центрируем элементы по вертикали */
}

.input-username,
.input-password {
    padding: 10px; 
    border: 1px solid #0288d1; /* Цвет рамки */
    border-radius: 5px; 
    flex: 1; 
    min-width: 150px; 
    transition: border-color 0.3s; 
}

.input-username:focus,
.input-password:focus {
    border-color: #01579b; /* Цвет рамки при фокусе */
    outline: none; 
}

.create-user-button,
.login-button,
.logout-button {
    padding: 10px;
    background-color: #26a69a; /* Зеленый цвет для кнопок */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    flex-shrink: 0; 
    transition: background-color 0.3s;
}

.create-user-button:hover,
.login-button:hover,
.logout-button:hover {
    background-color: #00796b; 
}

.message-list {
    border: 1px solid #ccc;
    padding: 10px;
    height: 400px;
    overflow-y: scroll;
    margin-bottom: 15px;
    background-color: #f1f8e9; /* Светлый фон для списка сообщений */
    border-radius: 5px;
    display: flex;
    flex-direction: column; /* Обеспечиваем вертикальное выравнивание */
}

.input-message {
    padding: 10px; 
    border: 1px solid #0288d1; 
    border-radius: 5px; 
    margin-bottom: 10px; 
    flex: 1; 
    min-width: 0; 
    overflow: hidden; 
    resize: none; 
    box-sizing: border-box; 
    height: auto; 
    min-height: 40px; 
    font-size: 18px;
    transition: border-color 0.3s; 
}

.input-message:focus {
    border-color: #01579b; 
    outline: none; 
}

.send-button {
    padding: 10px;
    background-color: #42a5f5; /* Синий цвет для кнопки отправки */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px; 
    transition: background-color 0.3s; 
}

.send-button:hover {
    background-color: #1976d2;
}

.error-message {
    color: #d32f2f; /* Красный цвет для ошибок */
    margin-top: 10px;
}

.message {
    word-wrap: break-word; 
    overflow-wrap: break-word; 
    max-width: 80%; /* Ограничиваем ширину сообщений */
    padding: 10px; 
    margin-bottom: 5px; 
    border-radius: 10px; 
}

.current-user {
    background-color: #dcf8c6; /* Цвет фона для сообщений текущего пользователя */
    align-self: flex-end; /* Выравнивание вправо */
    border-radius: 10px 10px 0 10px; /* Скругление углов */
    margin-left: auto; /* Отодвигаем сообщение к правому краю */
}

.other-user {
    background-color: #ECE0F8; /* Цвет фона для сообщений других пользователей */
    align-self: flex-start; /* Выравнивание влево */
    border-radius: 10px 10px 10px 0; /* Скругление углов */
    margin-right: auto; /* Отодвигаем сообщение к левому краю */
}

.context-menu {
    position: absolute;
    background: white;
    border: 1px solid #ccc;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
    z-index: 1000;
}

.context-menu button {
    display: block;
    padding: 8px 12px;
    border: none;
    background: none;
    cursor: pointer;
    width: 100%;
    text-align: left;
}

.context-menu button:hover {
    background-color: #f0f0f0;
}

.message-avatar {
    width: 35px; /* Ширина аватара */
    height: 35px; /* Высота аватара */
    border-radius: 50%; /* Круглая форма */
    object-fit: cover; /* Обрезка изображения */
    margin-right: 10px; /* Отступ справа */
    display: inline-block; /* Отображение в строке */
    background-color: #e0e0e0; /* Цвет фона по умолчанию */
    display: flex; /* Используем флексбокс для центрирования текста */
    align-items: center; /* Вертикальное выравнивание */
    justify-content: center; /* Горизонтальное выравнивание */
}

.message-content {
    margin-left: 5px; /* Отступ слева от двоеточия */
}