body {
  background-color: #66CDAA; /* Мягкий светло-синий фон для всего приложения */
  margin: 0; /* Убираем стандартные отступы браузера */
  font-family: 'Roboto', sans-serif; /* Убедитесь, что шрифт установлен */
}

.app {
  max-width: 1000px;
  margin: 20px auto; 
  padding: 20px;
  background: linear-gradient(135deg, #e1f5fe, #bbdefb); /* Градиентный фон */
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  font-family: 'Roboto', sans-serif; 
  height: 100%;
  overflow: auto; 
}

h1 {
  text-align: center;
  color: #0d47a1; /* Тёмно-синий цвет для заголовка */
  margin-bottom: 20px;
  font-weight: 700; 
}
